button {
  border: none;
  background: #1E1E1D;
  font-size: 1.5vw;
  border: 1px solid #2FD0B855;
  color: rgb(255, 255, 255);
  font-weight: bold;
  cursor: pointer;
  border-radius: 50%;
  outline: none;
  width: 3.5vw;
  height: 3.5vw;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .5s ease;
}

button:hover {
  background: #828ca000;
  box-shadow: 0px 0px 2px 1px #2FD0B855;
   transition: all .5s ease;
}

.equals {
  color: red;
}

.Clear {
  color: #2FD0B8;

}

.Symbol {
  color: #6cb649;
}
.MathSymbol {
  color: #d0cb2f;
}
.Period {
  color: #df3f20;
}

.equals:hover {
  background-color: rgb(228, 39, 15);
  color: #FFF;
}

.undo{
  transform: rotateZ(-45deg);
}
.undo span{
  position: absolute;
  transition: all 1s ease;
  
  visibility: visible;
}
.undo:hover span {
  transition: all .01s ease;
  visibility: hidden;
}

.undo:after{
  content:'Undo'; 
  transition: all .01s ease;
  
  position: absolute;
  visibility: hidden;
  font-size: 1vw;
  transform: rotateZ(42deg);
}
.undo:hover:after{
  
  visibility: visible;
}