.SideNav{

	position: fixed;
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100%;
	width: 6vw;
	background:  #040404;
	
}

.SideNav-Logo{

	position: relative;
	height: 15%;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	
}

.SideNav span {
	width: 80%;
}
.SideNav-Logo a{
	color: white;
	cursor: pointer;
	text-align: center;
	font-size: 0.7vw;
}

.SideNav-Logo img{
	width: 70% ;
	transition: all 2s ease;
	margin-top: 1vw;
}
.SideNav-Logo img:hover{
	animation: LogoShake 2s linear infinite;
}

.SideNav-list {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 60%;
	
	
}
.SideNav-Unorderedlist{
	
	width: 80%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	padding-inline-start: 0px;
	
}
.SideNav-list li{
	
	display: inline-flex;
	justify-content: center;
	align-items: center;
	height: 2.5vw;
	margin: .5vw 0vw;
	padding: 0vw .1vw;
	list-style: none;
	transition: all .5s ease;
}

.SideNav-list li:hover{
	transform: scale(1.15);
}
.SideNav a{
	text-decoration: none;
	color: white;
}


.SideNav-text{
	font-size: 1vw;
	display: none;
	color: white;
	margin: 0 1px 0 1px;

}

li:hover .SideNav-text{
	display: block;
}

.SideNav-icon{
	display: block;
	font-size: 1.8vw;
	color: white;

}

li:hover .SideNav-icon{
	display: none;
	
}



.SideNav-Contact-Information{
	width: 100%;
	height: 25%;

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.SideNav-Contact-Method{
	height: 100%;
	border-radius: 10px;
	margin: .5vw 0vw;
}


.SideNav-Contact-Information a{
	font-size: 1vw;
	display: flex;
	flex-direction: column;
	align-items: center;
	border-radius: 10px;


	



}
.SideNav-Contact-text{
	font-size: 0.7vw;
	color: white;
	opacity: 0;
	transition: all 1s ease;
	

}
.SideNav-Contact-Information a:hover .SideNav-Contact-text{
	opacity: 1;
	transition: all 1s ease;
}
.SideNav-Contact-Information a:hover{
	transform: scale(1.2);
}
.SideNav-Contact-Icon{
	color: white;
	margin: 2px;
}



@keyframes LogoShake {
  0% {
    transform: rotateZ(0deg);
  }
  25% {
  	transform: rotateZ(45deg)
  }
  50% {
  	transform: rotateZ(0deg)
  }
  75% {
  	transform: rotateZ(-45deg)
  }
  100% {
    transform: rotateZ(0deg)
  }
}