.WebsitesAbout {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 80%;
	height: 20vh;
	border-radius: 3em;
	margin-top: 20vh;
	background: linear-gradient(90deg, rgba(176,106,193,1) 11%, rgba(67,101,137,1) 48%, rgba(94,67,129,1) 57%, rgba(66,119,118,1) 93%);
	box-shadow: 0px 0px 3px 5px rgba(31,41,45,1);
	padding: 1em;
	transition: all 2s ease;
}
.WebsitesAbout-NotHidden{
	height: 20vh;
}
.WebsitesAbout-Hidden{
	height: 5vh;
}

.WebsitesAbout-website {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	position: absolute;
	height: 100%;
	width: 45%;
	left: 2%;
	bottom: 0;
	transition: all .5s ease;
}

.WebsitesAbout-Website {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	position: absolute;
	height: 100%;
	width: 45%;
	right: 2%;
	bottom: 0;
	transition: all .5s ease;
	
}
.WebsitesAbout-website h2, .WebsitesAbout-Website h2{
	font-size: 1.3em;

}
.WebsitesAbout-website p, .WebsitesAbout-Website p{
	font-size: 1em;
	color: white;
	
}

.Hidden-Text {
	visibility: hidden;
	opacity: 0;
}
.NotHidden-Text {
	transition-delay: 1.5s;
	visibility: visible;
	opacity: 1;
	
}
.WebsitesAbout-Button {
	transition: all 1s eases;
}