



.Landing{
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: calc(100%-6vw);
	margin-left: 6vw;
	height: 100%;
	padding:.5em;
	background: #1E1E1D;
	align-items: center;
}


.LandingCanvas{
  
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 100%;
	height: 100%;
	align-items: flex-start;
	margin-top: 20vh;
  

}
.greeting{
	transform: translateX(10%) ;
	width: 60%;
	margin-left: 2vw;
}
.Letter {
  font-size: 10vw;
  font-size: 4vmax;
  max-width: 100px;
  font-weight: 700;
  position: relative;
}
.Hidden{
	opacity: 0;
}

.SingleWord{
	display: inline-flex;
}


.One {
	animation: Bop 1.8s ease-in-out;
}
.Two {
	animation: Shake 1.8s ease-in-out;
}
.Three{
	animation: Swirl 1.8s ease-in-out;
}
.Four {
	animation: Sway 1.8s ease-in-out;
}



.One:after {
  position: absolute;
  content: attr(content);
  top: 0;
  left: 0;
  color: white;
  -webkit-transition: all .5s;
  -moz-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-transform-origin: bottom right;
  -ms-transform-origin: bottom right;
  transform-origin: bottom right;
  animation: ColorOne 2s ease-in-out;
}
.Two:after {
  position: absolute;
  content: attr(content);
  top: 0;
  left: 0;
  color: white;
  -webkit-transition: all .5s;
  -moz-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-transform-origin: bottom left;
  -ms-transform-origin: bottom left;
  transform-origin: bottom left;
  animation: ColorTwo 2s ease-in-out;
}
.Three:after {
  position: absolute;
  content: attr(content);
  top: 0;
  left: 0;
  color: white;
  -webkit-transition: all .5s;
  -moz-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-transform-origin: top right;
  -ms-transform-origin: top right;
  transform-origin: top right;
  animation: ColorThree 2s ease-in-out;
}
.Four:after {
  position: absolute;
  content: attr(content);
  top: 0;
  left: 0;
  color: white;
  -webkit-transition: all .5s;
  -moz-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-transform-origin: top left;
  -ms-transform-origin: top left;
  transform-origin: top left;
  animation: ColorFour 2s ease-in-out;
}
.One:hover:after {
	color: #2FD0B8;
}
.Two:hover:after {
	color: #2F97D0;
}
.Three:hover:after {
	color: #2FD068;
}
.Four:hover:after {
	color: #D02F47;
}

.Letter:hover:after {
  -webkit-transform: skew(5deg, 15deg);
  -ms-transform: skew(5deg, 15deg);
  transform: skew(5deg, 15deg);
}



.greeting-footer{
	color: white;
  cursor: default;
}

.footer-dash:hover{
	color: #D02F47;
}

.footer-CSS3:hover{
	color: #2F97D0;
}

.footer-Javascript:hover{
	color: #2FD068;
}

.footer-React:hover{
	color: #2FD0B8;
}




.bg-text-word{
	position: absolute;
	color: white;
	opacity: 0.4;
	font-family: 'Comforter', cursive;
	font-size: 2vw;
	animation: Appear 2.5s ease-in-out;
  cursor: default;
	
}

.-body{
	transform: translateY(-180%) translateX(-160%);
}
.-h1{
	transform: translateX(-150%);
}
.-h1C{
	transform: translateY(100%) translateX(50%);
}
.-p{
	transform: translateY(180%) translateX(-1850%);
}
.-pC{
	transform: translateY(-60%) translateX(-120%); ;
}
.-bodyC{
	transform: translateY(120%) translateX(-135%); ;
}


.button{
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 1px solid #2FD0B8;
	padding: .3em 1em;
	color: white;
	font-size: 2vw;
	color: #2FD0B8;
	transform: translateY(0%) translateX(20%);
	transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);	
	cursor: pointer;
	user-select: none;
	border-radius: 10vw;
}
.button a {
  color: #2FD0B8;
}

.button:after {
	content: '';
	border-radius: 10vw;
	position: absolute;	
	transition: inherit;
	z-index: -1;
}

.button:hover {
	
	border: 1px solid #000;
	transition-delay: .1s;
}
.button:hover a {
  color: #FFF;
  transition-delay: .1s;
}


.button:hover:after {
	background: #2FD0B8;
	transition-delay: .1s;
	border: 1px solid #000;
}

/*from left*/ 
.from-left:after {

	
	width: 0;
	height: 100%;
}


.from-left:hover:after {
	width: 100%;
}

.Landing-logo{
  width: 30%;
	margin-right: 5%;
	
}
.Landing-logo-img{
  
	width: 30vw;
}

.scrollDown{
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 100%;
	writing-mode: vertical-rl;
	font-size: 1vw;
	font-weight: 500;
}



.scrollDown-item a{
	display: flex;
	flex-direction: row;
	align-items: center;
	cursor: pointer;
	color: white;
}

.scrollDown p {
	animation: vary 1s linear infinite;
}
@keyframes vary {
	0% {
    transform: translateY(-10%);
  }
  
  50% {
  	transform: translateY(0%);
  }
  100% {
    transform: translateY(-10%);
  }
}

@keyframes Bop {
	0% {
		color: #2FD0B8;
    transform: translateY(-35px);
  }
  20% {
  	transform: translateY(5px) scaleY(0.8) scaleX(1.1);
  }
  40% {
    transform: translateY(-1.9px) scaleY(1.1);
  }

  60% {
    transform: translateY(1px) scaleY(0.95);
  }
  100% {
  	color: #FFF;
    transform: translateY(0px) scaleY(1);
  }
}

@keyframes Shake {
	0% {
    transform: rotateZ(0deg) scale(0.7);
  }
  20% {
    transform: rotateZ(-10deg);
  }
  40% {
    transform: rotateZ(10deg);
  }
  60% {
    transform: rotateZ(-10deg);
  }
  80% {
    transform: rotateZ(10deg);
  }
  100% {
    transform: rotateZ(0deg) scale(1);
  }
}

@keyframes Swirl {
	0% {
		opacity: 0.1;
    transform: scale(0.7);
  }
  50% {
  	opacity: 0.5;
    transform: scale(1.1);
  }
  100% {
  	opacity: 1;
    transform: scale(1);
  }
}

@keyframes Sway {
	0% {
    transform: translateY(0px) scaleY(1.0);
  }
  50% {
    transform: translateY(5px) translateX(-2px) scaleY(0.8);
  }
  100% {
    transform: translateY(0px) scaleY(1.0);
  }
}

@keyframes ColorOne {
	0% {
    color: #2FD0B8;
  }
  80% {
    color: #2FD0B8;
  }
  100% {
    color: #FFF;
  }
}
@keyframes ColorTwo {
	0% {
    color: #2F97D0;
  }
  80% {
    color: #2F97D0;
  }
  100% {
    color: #FFF;
  }
}
@keyframes ColorThree {
	0% {
    color: #2FD068;
  }
  80% {
    color: #2FD068;
  }
  100% {
    color: #FFF;
  }
}
@keyframes ColorFour {
	0% {
    color: #D02F47;
  }
  80% {
    color: #D02F47;
  }
  100% {
    color: #FFF;
  }
}

@keyframes Appear {
	0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}
