	.ContactMe{

			width: calc(100%-6vw);
			margin-left: 6vw;
			height: 100vh;
			background: #1E1E1D;
			display: flex;
			flex-wrap: wrap;
			flex-direction: row;
		}

	.ContactMe-Container {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		width: 100%;
		padding: 2vw;
	}

	.ContactMe-Section {
		
		position: relative;
		display: flex;
		flex-direction: column;
		width: 49%;
		border-radius: 2vw;
		transition: all 1s ease;
		padding: 2em;

	}

	.ContactMe-Title {
		margin-bottom: 1vw;
		text-align: center;
		height: 15%;
	}
	.ContactMe-Title span{
		font-family: 'Raleway', sans-serif;
		font-size: 4vw;
		margin-left: 1vw;
		letter-spacing: 5px;
	    color: #2FD0B8;
	    text-shadow: rgb(150, 150, 150) -4px 2px 5px;
		animation: Reveal 1s ease;
	}

	.ContactMe-Discription {
		width: 100%;
		margin-bottom: 1vw;
		position: relative;
		height: auto;
		padding: 1em;
	}
	.ContactMe-Discription span{
		color: #DDD;
		font-size: 1.2vw;
		
	}
	.Logo-Control {
		position: relative;

	}
	.Hint-Logo {
		position: absolute;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		width: 1.5vw;
		height: 1.5vw;
		box-shadow: 0px 0px 4px 1px  #2FD0B8;
		top: 0;
		right: -10%;
		transition: all 1s ease;
		
	}
	.Hint-Logo:hover {
		transform: scale(1.1);
		color: #2FD0B8;
		box-shadow: 0px 0px 4px 1px  rgb(150, 150, 150);
	}

	.Visa{
		position: absolute;
		box-shadow: 0px 0px 4px 1px  rgb(150, 150, 150);
		width: 90%;
		height: 7em;
		left: 0;
		background: #1E1E1D;
		visibility: hidden;
		opacity: 0;
		border-radius: 2vw;
		transition: all 1s ease;
		padding: .5em;
	}
	.Hint-Logo:hover+.Visa{
		visibility: visible;
		opacity: 1;
		transition: all 1s ease;
	}

	.Visa-Header {
		text-align: center;
		font-size: 1vw;
		height: 20%;
	}
	.Visa-List {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 80%;
	}
	.Visa-List-Section {
		position: relative;
		font-size: .8vw;
		width: 30%;
	}
	.Visa-List-Section img {
		box-shadow: 0px 0px 2px 1px  rgb(150, 150, 150);
		border-radius: 1vw;
		width:4vw;
	}
	.Nationality {
		position: absolute;
		justify-content: center;
		align-items: center;
		display: flex;
		border-radius: 1vw;
		width: 4vw;
		height: 95%;
		left: 0;
		top: 0;
		background: rgba(90,90,90,0.2);
		transition: all 1s ease;
	}
	.Nationality span {
		transform: rotateZ(-60deg);
		font-size: 1.3vw;
		cursor: default;
	}
	.Visa-List-Section:hover .Nationality {
		visibility: visible;
		opacity: 1;
	}


	.ContactMe-Contact {
		position: relative;
		
		display: flex;
		justify-content: space-between;
		padding: 1em;
		flex-wrap: wrap;
		height: 15%;
		
	}
	.ContactMe-Contact-Method {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		box-shadow: 0px 0px 4px 1px  #2FD0B8;
		width: 4vw;
		height: 4vw;
		border-radius: 50%;
		transition: all 1s ease;
	}
	.ContactMe-Contact-Method:hover {
		
		box-shadow: 0px 0px 4px 1px  #DDD;
		background: #2FD0B8AA;
	}
	.ContactMe-Contact-Method a {
		width: 4vw;
		height: 4vw;
		display: inherit;
		align-items: center;
		justify-content: center;
		font-size: 2vw;
	}
	
	.Contactme-Contact-text{
		position: absolute;
		text-align: center;
		width: 5vw;
		font-size: 1vw;
		bottom: -55%;
		left: -14%;
		visibility: hidden;
		opacity: 0;
		position: absolute;
		color: #FFF;
		transition: all 1s ease;
	}
	a:hover .Contactme-Contact-text {
		
		visibility: visible;
		opacity: 1;
		
		
	}


	.Map-Wrapper {
		position: relative;
		width: 100%;
		height: 100%;
	}

	.Map-Address {
		
		position: absolute;
		width: 40%;
		background: #1E1E1DCC;
		height: auto;
		top: 7%;
		left: 7%;
		color: white;
		padding: 1em;
		font-size: 1.3vw;
		border-radius: 1em;

	}
	.Map-Address span {
		color: #2FD0B8;
		
	}

















	.ContactMe-Text{
		box-shadow: -5px 0px 10px 0px  #2FD0B8;
	}
	.ContactMe-Section:Hover {
		box-shadow: 5px 0px 10px 0px  #2FD0B8;
	}
	.ContactMe-Map{
		box-shadow: 5px 0px 10px 0px  #2FD0B8;
	}
	.ContactMe-Map:Hover {
		box-shadow: -5px 0px 10px 0px  #2FD0B8;
	}