/*	The main profile (body of the profile component)
	Contains 4 divisions equally distributed to quarters
*/
	.Profile{

		width: calc(100%-6vw);
		margin-left: 6vw;
		height: 100vh;
		background: #1E1E1D;
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		justify-content: space-between;
		align-items: space-between;
		padding: 1em;

	}

/*	-The Containers that hold sections: ;
		1- About me
		2- Education
		3- Skills
		4- Experience
*/
	.Profile-Section-Container{
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 1vw;
		width: 48%;
		height: 48%;
		color: white;
		
		border-radius: 1em;

	}

/*	-This container holds 5 divisions, the first is for default display.
	-First devision changes on hover
	-Other 4 divisions display when react hooks (AboutActive) is activated
*/
	.Profile-Section{
		position: relative;
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		height: 100%;
		color: white;
		justify-content: center;
		align-items: center;
		border: 1px #383836 solid;
		border-radius: 1em;
		transition: all 1s ease;
	}
/* scaling the division on hover */
	.Profile-Section:hover{

		transform: scale(1.006);
	}

/*Setting shadows and moving them on hover*/
	.About{
		box-shadow: -5px -5px 10px 0px  #2FD0B8;
		
	}
	.About:hover{
		box-shadow: 5px 5px 10px 0px  #2FD0B8;
	}
	.Education{
		box-shadow: 5px -5px 10px 0px  #2FD0B8;
	}
	.Education:hover{
		box-shadow: -5px 5px 10px 0px  #2FD0B8;
	}
	.Skills{
		box-shadow: -5px 5px 10px 0px  #2FD0B8;
	}
	.Skills:hover{
		box-shadow: 5px -5px 10px 0px  #2FD0B8;
	}
	.Experience{
		box-shadow: 5px 5px 10px 0px  #2FD0B8;
	}
	.Experience:hover{
		box-shadow: -5px -5px 10px 0px  #2FD0B8;
	}

/*	-The 5 divisions inside of Profile-Section  
	-on Click on main div, the first of the 5 dissapears and the other 4 show
*/
	.Profile-innerSection{
		position: relative;
		display: flex;
		width: 50%;
		height: 50%;
		justify-content: center;
		align-items: center;
		border-radius: 1em;
	}

/*	-The first of the 5 divisions.
	-Contains hover animations 
	-Contains InnerSection-MainTitle and InnerSection-Discription
*/
	.InnerSection-Details{
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		text-align: center;
		width: 100%;
		height: 100%;
		border-radius: 1em;
	}
	.InnerSection-MainTitle{
		
		font-family: 'Raleway', sans-serif;
	    text-align: center;
	    font-size: 3.0vw;
	    letter-spacing: 1vw;
	    word-spacing: 12px;
	    color: #2FD0B8;
	    text-shadow: rgb(150, 150, 150) -4px 2px 5px;
	    transform: translateY(7vh);
	    transition: all 1s ease;	
	    animation: breathe 2s ease-in-out;

	}
	.InnerSection-Details:hover .InnerSection-MainTitle{
		opacity: 1;
		font-size: 1.5em;
		transform: translateY(0vh);
		cursor: default;

	}
	.InnerSection-Discription{
		padding: 0 1em;
		opacity: 0;
		transition: all .2s ease ;
		font-size: 1.5vw;
		cursor: default;

	}
	.InnerSection-Details:hover .InnerSection-Discription{
		opacity: 1;
		transition: all 1s ease ;
	}

/*	-The 4 inner sections that display upon clicking on the Main container
	-Contains InnerSection-Title & InnerSection list

*/
	.InnerSection {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		height: 100%;

	}
	.profile-close{
		position: absolute;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 2vw;
		text-align: center;
		top: 1vw;
		right: 1vw;
		border-radius: 50%;
		cursor: pointer;
		z-index: 999;
		visibility: hidden;
		opacity: 0;
	}
	.profile-close-visible{
		visibility: visible;
		opacity: 1;
		transition: all 2s ease;
	}

	.InnerSection-Skills {
		justify-content: flex-start;
	}
	.InnerSection-Title{	
		display: flex;
		align-items: center;
		font-family: 'Raleway', sans-serif;
	    text-align: center;
	    font-size: 2.5vw;
	    letter-spacing: 5px;
	    color: #2FD0B8;
	    text-shadow: rgb(150, 150, 150) -4px 2px 5px;
	    transition: all 1s ease;
	    height: 15%;
	    margin-top: 1vw;
	    margin-bottom: 1vw;
	    animation: Reveal 1s ease;
	    padding: 1vw;
	    border-radius: 2vw;
	    box-shadow: 0px 0px 10px 2px  #FFF;
	    cursor: default;
	}
	.InnerSection-Text {
		height: 80%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		cursor: default;
	}
	.InnerSection-Skills .InnerSection-Title {
		margin: 1vw 0vw 1vw 0vw;
	}

	.InnerSection p{
		display: flex;
		justify-content: center;
		width: 90%;
		font-size: 1.3vw;
		text-align: center;
		list-style: none;
		animation: Reveal 1.5s ease;
		margin: 1vw;
	}
	/*Button for extra information*/
	.Profilebutton{
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid #2FD0B8;
		color: white;
		padding: .5em 3em;
		font-size: 1vw;
		color: #2FD0B8;
		transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);	
		cursor: pointer;
		user-select: none;
		border-radius: 10vw;
		animation: Reveal 1.5s ease;
		margin-bottom: 1vw;
	}
	.Profilebutton:after {
		content: '';
		border-radius: 10vw;
		position: absolute;	
		transition: inherit;
		z-index: -1;
	}	

	.Profilebutton:hover {
		color: #FFF;
		border: 1px solid #000;
		transition-delay: .1s;
	}


	.Profilebutton:hover:after {
		background: #2FD0B8;
		transition-delay: .1s;
		border: 1px solid #000;
	}

/*from left*/ 
	.from-left:after {
		width: 0;
		height: 100%;
	}


	.from-left:hover:after {
		width: 100%;
	}

/*Extra list inside the inner section*/
	.inner-knowMore-title {
		padding: 1em;
	}
	.inner-knowMore-list{
		position: absolute;
		visibility: hidden;
		opacity: 0;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		background: #1E1E1D;
		border-radius: 1em;
		box-shadow: -5px -5px 10px 0px  #2FD0B8;
		transition: all .3s ease;
		width: 35vw;
		height: 13vw;
		transform: translate(-46%,-119%);
	}
	.inner-knowMore-list:hover {
		box-shadow: 5px 5px 10px 0px  #2FD0B8;
	}
	

	.Profilebutton:hover .inner-knowMore-list{
		visibility: visible;
		opacity: 1;
		transition: all 1s ease;
		
	}
	.inner-knowMore-details{
		display: inherit;
		width: 35vw;
		flex-wrap: wrap;
		justify-content: center;

	}
	.inner-knowMore-details div{
		display: flex;
		justify-content: center;
		align-items: center;
		margin: .3em;
		border: 1px solid #2FD0B8;
		border-radius: 1vw;
		font-size: 1vw;
		height: 3vw;
		padding: .1vw .5vw;

	}

/*Skills tab CSS*/
	.Skills-list{
		display: flex;
		width: 90%;
		height: 65%;
		flex-wrap: wrap;
		justify-content: center;
		animation: Reveal 1.5s ease;

	}
	.Skills-list div{
		display: flex;
		justify-content: center;
		align-items: center;
		margin: .3em;
		border: 1px solid #2FD0B8;
		border-radius: 1vw;
		font-size: 1vw;
		width: auto;
		height: 3vw;
		padding: .1vw .5vw;
		transition: all 2s ease;
	}
	.Skills-list span{
		position: absolute;
		display: flex;
		border: 1px red solid;
		bottom: 0;
		height: 15%;
		width: 80%;
		align-items: center;
		justify-content: flex-start;
		bottom: 1vw;
		padding: 0 0 0 1em;

	}

	.Skill-discription{
		visibility: hidden;
		opacity: 0;
		transition: all 1s ease;
	}

	.Skills-list div:hover+span{
		visibility: visible;
		opacity: 1;
		
		transition: all 1s ease;
	}


/*	Used to hide and display sections using React hooks
*/
	.Hidden-Section{
		display: none;
	}
	.Hidden-Not{
		display: all;
	}
	.Profile-Section-Hidden{
		display: none;
	}
	.Profile-Section-Fill{
		transition: all .5s linear ;
		width: 100vw;
		height: 100%;	
	}
	.Profile-innerSection-Fill{
		width: 100%;
		height: 100%;	
	}


/*	The Inner section (Know more) animated text
*/
	.InnerSection-Scrolldown{
		position: absolute;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		width: 100%;
		writing-mode: vertical-rl;
		bottom: 0vw;
		right: 0vw;
		border-radius: 1em;
	}
	.InnerSection-Scrolldown-item{

		transform: rotateZ(-45deg);

	}
	.InnerSection-Scrolldown-item p{
		display: flex;
		flex-direction: row;
		align-items: center;
		cursor: cell;
		color: white;
		font-size: .6vw;
		letter-spacing: .2vw;
		animation: vary 1s linear infinite;
	}


/*KeyFrames*/

/*vary : used in (.InnerSection-Scrolldown p)*/
	@keyframes vary {
		0% {transform: translateY(-10%);}
		50% {transform: translateY(0%);}
		100% {transform: translateY(-10%);}
	}

/*Reveal : used in (.InnerSection pSkills-list)*/
	@keyframes Reveal {
		0% {opacity: 0;}
		50% {opacity: 0;}
		100% {opacity: 1;}
	}
/*breathe : used in .InnerSection-MainTitle*/
	@keyframes breathe {
		0% {opacity: 0.1; transform: translateY(7vh);}
		50% {transform: scale(1.5);}
		100% {opacity: 1; transform: translateY(7vh) scale(1.0); }
	}