.GamesNavigation {
	position: fixed;
	width: 80%;

}


.GamesNav{
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	height: 10vh;
	width: 100%;
	background: linear-gradient(90deg, rgba(129,23,36,1) 10%, rgba(173,40,81,1) 27%, rgba(94,31,51,1) 50%, rgba(173,40,81,1) 70%, rgba(129,23,36,1) 96%);
	padding: 0em 1.5em;
	margin: 2em 0 0 0;
	border-radius: 2em;
	animation: DropDown 2s ease-in-out;
	z-index: 1;
	
}

.GamesNav-Profolio {
	width: 5%;
	display: flex;
	justify-content: center;
	
}


.GamesNav-list {
	width: 60%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}
.GamesNavList {
	display: flex;
	justify-content: space-between;
	width: 100%;
}

.GamesNav-list li , .GamesNav-Profolio li{
	display: inline-flex;
	justify-content: center;
	align-items: center;
	width: 55px;
	height: 1.9rem;
	list-style: none;
	transition: all .5s ease;
}

.GamesNav-list li:hover ,
.GamesNav-Profolio li:hover {
	transform: scale(1.15);
}
a{
	text-decoration: none;
	color: black;
}


.GamesNav-text{
	font-size: .9em;
	display: none;
	color: white;
	margin: 0 1px 0 1px;

}

li:hover .GamesNav-text{
	display: block;
}

.GamesNav-icon{
	display: block;
	font-size: 1.5em;
	color: white;

}

li:hover .GamesNav-icon{
	display: none;
	
}



.GamesNav-Logo{
	width: 5%;
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	
}

.GamesNav-Logo a{
	color: white;
	cursor: pointer;
	text-align: center;
	font-size: .5em;
}

.GamesNav-Logo img{
	width: 40px ;
	transition: all 2s ease;
}
.GamesNav-Logo img:hover{
	animation: LogoShake 3s linear infinite;
}

.GamesNav-Floor {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 15vh;
	box-shadow: 0 0 30px 2px #000 inset;
	top: 11%;
	left: 0%;
	transform-origin: bottom;
	transform: rotateX(-80deg) rotateZ(-7deg) rotateY(-1.2deg);
	z-index: -1;
}
.GamesNav-Floor-Shadow {
	height: 0;
	width: 100%;
	border-radius: 1em;
	animation: Shadow 2s ease-in-out;
	box-shadow: 0 0 25px 5px #000 ;

}

@keyframes LogoShake {
  0% {
    transform: rotateZ(0deg);
  }
  25% {
  	transform: rotateZ(45deg)
  }
  50% {
  	transform: rotateZ(0deg)
  }
  75% {
  	transform: rotateZ(-45deg)
  }
  100% {
    transform: rotateZ(0deg)
  }
}

@keyframes DropDown {
  0% {
    transform: translateY(-8em);
  }
  25% {
  	transform: translateY(0.4em) scaleY(0.8) ;
  }
  50% {
  	transform: translateY(-0.2em);
  }
  75% {
  	transform: translateY(0em) scaleY(1) ;
  }
  100% {
    
  }
}

@keyframes Shadow {
  0% {
    box-shadow: 0 0 20px 5px  ;
  }
 	25% {
  	box-shadow: 0 0 10px 2px #000 ;
  }
  50% {
  	box-shadow: 0 0 30px 10px #000 ;
  }
 	75% {
    box-shadow: 0 0 25px 5px #000 ;
  }
  100% {
  	box-shadow: 0 0 25px 5px #000 ;
  }
}