.screen {
  position: relative;
  height: 15%;
  width: 100%;
  padding: 0 10px;
  background-color: #485d70;
  border-radius: 10px;
  margin-bottom: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  color: white;
  font-weight: bold;
  box-sizing: border-box;
  transition: all .3s ease;
  
}

.screen-line {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 40%;
  width: 50%;

}
.screen-value {
  justify-content: flex-start;
  width: 50%;
  
}

.screen-result {
  position: absolute;
  bottom: 5%;
  right: 5%;
  color: #6cb649;
}
/*.screen span {
  font-size: 4vw;
  border: 1px red solid;
}*/

.screen:hover {
  box-shadow: 0px 0px 2px 1px #AAA;
  transition: all .3s ease;
}

.screen-pastresult {
 
  position: absolute;
  width: 50%;
  top: 5%;
  right: 0%;
  padding-right: 5%;
  color: #2FD0B8;
}
