.Navigation {
	position: fixed;
	width: 80%;
}


.Nav{
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	height: 10vh;
	width: 100%;
	background:  #040404;
	padding: 0em 1.5em;
	margin: 2em 0 0 0;
	border-radius: 2em;
	animation: DropDown 2s ease-in-out;

	
}

.Nav-Profolio {
	width: 5%;
	display: flex;
	justify-content: center;
	
}


.Nav-list {
	width: 60%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}
.NavList {
	display: flex;
	justify-content: space-between;
	width: 100%;
}

.Nav-list li , .Nav-Profolio li{
	display: inline-flex;
	justify-content: center;
	align-items: center;
	width: 55px;
	height: 1.9rem;
	list-style: none;
	transition: all .5s ease;
}

.Nav-list li:hover ,
.Nav-Profolio li:hover {
	transform: scale(1.15);
}
.Navigation a{
	text-decoration: none;
	color: black;
}


.Nav-text{
	font-size: .9em;
	display: none;
	color: white;
	margin: 0 1px 0 1px;

}

li:hover .Nav-text{
	display: block;
}

.Nav-icon{
	display: block;
	font-size: 1.5em;
	color: white;

}

li:hover .Nav-icon{
	display: none;
	
}



.Nav-Logo{
	width: 5%;
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	
}

.Nav-Logo a{
	color: white;
	cursor: pointer;
	text-align: center;
	font-size: .5em;
}

.Nav-Logo img{
	width: 40px ;
	transition: all 2s ease;
}
.Nav-Logo img:hover{
	animation: LogoShake 3s linear infinite;
}

.Nav-Floor {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 15vh;
	box-shadow: 0 0 30px 2px #000 inset;
	top: 11%;
	left: 0%;
	transform-origin: bottom;
	transform: rotateX(-80deg) rotateZ(-7deg) rotateY(-1.2deg);
	overflow: hidden;
}
.Nav-Floor-Shadow {
	height: 0;
	width: 100%;
	border-radius: 1em;
	animation: Shadow 2s ease-in-out;
	box-shadow: 0 0 25px 5px #000 ;

}

@keyframes LogoShake {
  0% {
    transform: rotateZ(0deg);
  }
  25% {
  	transform: rotateZ(45deg)
  }
  50% {
  	transform: rotateZ(0deg)
  }
  75% {
  	transform: rotateZ(-45deg)
  }
  100% {
    transform: rotateZ(0deg)
  }
}

@keyframes DropDown {
  0% {
    transform: translateY(-8em);
  }
  25% {
  	transform: translateY(0.4em) scaleY(0.8) ;
  }
  50% {
  	transform: translateY(-0.2em);
  }
  75% {
  	transform: translateY(0em) scaleY(1) ;
  }
  100% {
    
  }
}

@keyframes Shadow {
  0% {
    box-shadow: 0 0 20px 5px  ;
  }
 	25% {
  	box-shadow: 0 0 10px 2px #000 ;
  }
  50% {
  	box-shadow: 0 0 30px 10px #000 ;
  }
 	75% {
    box-shadow: 0 0 25px 5px #000 ;
  }
  100% {
  	box-shadow: 0 0 25px 5px #000 ;
  }
}