.WebsitesNavigation {
	position: fixed;
	width: 80%;
}


.WebsitesNav{
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	height: 10vh;
	width: 100%;
	background: linear-gradient(90deg, rgba(176,106,193,1) 20%, rgba(67,101,137,1) 38%, rgba(94,67,129,1) 59%, rgba(66,119,118,1) 83%);
	padding: 0em 1.5em;
	margin: 2em 0 0 0;
	border-radius: 2em;
	animation: DropDown 2s ease-in-out;
	z-index: 1;

	
}

.WebsitesNav-Profolio {
	width: 5%;
	display: flex;
	justify-content: center;
	
}


.WebsitesNav-list {
	width: 60%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}
.WebsitesNavList {
	display: flex;
	justify-content: space-between;
	width: 100%;
}

.WebsitesNav-list li , .WebsitesNav-Profolio li{
	display: inline-flex;
	justify-content: center;
	align-items: center;
	width: 55px;
	height: 1.9rem;
	list-style: none;
	transition: all .5s ease;
}

.WebsitesNav-list li:hover ,
.WebsitesNav-Profolio li:hover {
	transform: scale(1.15);
}
a{
	text-decoration: none;
	color: black;
}


.WebsitesNav-text{
	font-size: .9em;
	display: none;
	color: white;
	margin: 0 1px 0 1px;

}

li:hover .WebsitesNav-text{
	display: block;
}

.WebsitesNav-icon{
	display: block;
	font-size: 1.5em;
	color: white;

}

li:hover .WebsitesNav-icon{
	display: none;
	
}



.WebsitesNav-Logo{
	width: 5%;
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	
}

.WebsitesNav-Logo a{
	color: white;
	cursor: pointer;
	text-align: center;
	font-size: .5em;
}

.WebsitesNav-Logo img{
	width: 40px ;
	transition: all 2s ease;
}
.WebsitesNav-Logo img:hover{
	animation: LogoShake 3s linear infinite;
}

.WebsitesNav-Floor {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 15vh;
	box-shadow: 0 0 30px 2px #000 inset;
	top: 11%;
	left: 0%;
	transform-origin: bottom;
	transform: rotateX(-80deg) rotateZ(-7deg) rotateY(-1.2deg);
	z-index: -1;
}
.WebsitesNav-Floor-Shadow {
	height: 0;
	width: 100%;
	border-radius: 1em;
	animation: Shadow 2s ease-in-out;
	box-shadow: 0 0 25px 5px #000 ;

}

@keyframes LogoShake {
  0% {
    transform: rotateZ(0deg);
  }
  25% {
  	transform: rotateZ(45deg)
  }
  50% {
  	transform: rotateZ(0deg)
  }
  75% {
  	transform: rotateZ(-45deg)
  }
  100% {
    transform: rotateZ(0deg)
  }
}

@keyframes DropDown {
  0% {
    transform: translateY(-8em);
  }
  25% {
  	transform: translateY(0.4em) scaleY(0.8) ;
  }
  50% {
  	transform: translateY(-0.2em);
  }
  75% {
  	transform: translateY(0em) scaleY(1) ;
  }
  100% {
    
  }
}

@keyframes Shadow {
  0% {
    box-shadow: 0 0 20px 5px  ;
  }
 	25% {
  	box-shadow: 0 0 10px 2px #000 ;
  }
  50% {
  	box-shadow: 0 0 30px 10px #000 ;
  }
 	75% {
    box-shadow: 0 0 25px 5px #000 ;
  }
  100% {
  	box-shadow: 0 0 25px 5px #000 ;
  }
}