.GamesAbout {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 80%;
	height: 20vh;
	border-radius: 3em;
	margin-top: 20vh;
	background: linear-gradient(90deg, rgba(129,23,36,1) 11%, rgba(173,40,81,1) 42%, rgba(94,31,51,1) 51%, rgba(173,40,81,1) 61%, rgba(129,23,36,1) 83%);
	box-shadow: 0px 0px 3px 5px rgba(31,41,45,1);
	padding: 1em;
	transition: all 2s ease;
}
.GamesAbout-NotHidden{
	height: 20vh;
}
.GamesAbout-Hidden{
	height: 5vh;
}

.GamesAbout-Website {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	position: absolute;
	height: 100%;
	width: 45%;
	left: 2%;
	bottom: 0;
	transition: all .5s ease;
}

.GamesAbout-Game {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	position: absolute;
	height: 100%;
	width: 45%;
	right: 2%;
	bottom: 0;
	transition: all .5s ease;
	
}
.GamesAbout-Website h2, .GamesAbout-Game h2{
	font-size: 1.3em;

}
.GamesAbout-Website p, .GamesAbout-Game p{
	font-size: 1em;
	color: white;
	
}

.Hidden-Text {
	visibility: hidden;
	opacity: 0;
}
.NotHidden-Text {
	transition-delay: 1.5s;
	visibility: visible;
	opacity: 1;
	
}
.GamesAbout-Button {
	transition: all 1s eases;
}