.scrollDown{
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 100%;
	-ms-writing-mode: tb-rl;
	    writing-mode: vertical-rl;
	font-size: 1vw;
	font-weight: 500;
}



.scrollDown-item a{
	display: flex;
	flex-direction: row;
	align-items: center;
	cursor: pointer;
	color: white;
}

.scrollDown p {
	animation: vary 1s linear infinite;
}
@keyframes vary {
	0% {
    transform: translateY(-10%);
  }
  
  50% {
  	transform: translateY(0%);
  }
  100% {
    transform: translateY(-10%);
  }
}

@keyframes Bop {
	0% {
		color: #2FD0B8;
    transform: translateY(-35px);
  }
  20% {
  	transform: translateY(5px) scaleY(0.8) scaleX(1.1);
  }
  40% {
    transform: translateY(-1.9px) scaleY(1.1);
  }

  60% {
    transform: translateY(1px) scaleY(0.95);
  }
  100% {
  	color: #FFF;
    transform: translateY(0px) scaleY(1);
  }
}

@keyframes Shake {
	0% {
    transform: rotateZ(0deg) scale(0.7);
  }
  20% {
    transform: rotateZ(-10deg);
  }
  40% {
    transform: rotateZ(10deg);
  }
  60% {
    transform: rotateZ(-10deg);
  }
  80% {
    transform: rotateZ(10deg);
  }
  100% {
    transform: rotateZ(0deg) scale(1);
  }
}

@keyframes Swirl {
	0% {
		opacity: 0.1;
    transform: scale(0.7);
  }
  50% {
  	opacity: 0.5;
    transform: scale(1.1);
  }
  100% {
  	opacity: 1;
    transform: scale(1);
  }
}

@keyframes Sway {
	0% {
    transform: translateY(0px) scaleY(1.0);
  }
  50% {
    transform: translateY(5px) translateX(-2px) scaleY(0.8);
  }
  100% {
    transform: translateY(0px) scaleY(1.0);
  }
}

@keyframes ColorOne {
	0% {
    color: #2FD0B8;
  }
  80% {
    color: #2FD0B8;
  }
  100% {
    color: #FFF;
  }
}
@keyframes ColorTwo {
	0% {
    color: #2F97D0;
  }
  80% {
    color: #2F97D0;
  }
  100% {
    color: #FFF;
  }
}
@keyframes ColorThree {
	0% {
    color: #2FD068;
  }
  80% {
    color: #2FD068;
  }
  100% {
    color: #FFF;
  }
}
@keyframes ColorFour {
	0% {
    color: #D02F47;
  }
  80% {
    color: #D02F47;
  }
  100% {
    color: #FFF;
