.AppsAbout {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 80%;
	height: 20vh;
	border-radius: 3em;
	margin-top: 20vh;
	background: linear-gradient(0deg, rgba(31,41,73,1) 0%, rgba(23,48,129,1) 72%);
	box-shadow: 0px 0px 3px 5px rgba(31,41,45,1);
	padding: 1em;
	transition: all 2s ease;
}
.AppsAbout-NotHidden{
	height: 20vh;
}
.AppsAbout-Hidden{
	height: 5vh;
}

.AppsAbout-Website {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	position: absolute;
	height: 100%;
	width: 45%;
	left: 2%;
	bottom: 0;
	transition: all .5s ease;
}

.AppsAbout-App {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	position: absolute;
	height: 100%;
	width: 45%;
	right: 2%;
	bottom: 0;
	transition: all .5s ease;
	
}
.AppsAbout-Website h2, .AppsAbout-App h2{
	font-size: 1.3em;

}
.AppsAbout-Website p, .AppsAbout-App p{
	font-size: 1em;
	color: white;
	
}

.Hidden-Text {
	visibility: hidden;
	opacity: 0;
}
.NotHidden-Text {
	transition-delay: 1.5s;
	visibility: visible;
	opacity: 1;
	
}
.AppsAbout-Button {
	transition: all 1s eases;
}