.GamesList {
	width: 100%;
	height: 60vh;
}

.GamesList-Container {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		width: 100%;
		height: 80%;
		padding: 1em;
		
	}

	.SingleGame {

		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-right: 1em;
		width: 15%;
		height: 100%;
		border-radius: 3em;
		transition: all 1s ease;
	}
	
	.SingleGame:hover {
		box-shadow: 0px 0px 4px 2px  #FFF;
		transform: scale(1.05);
	}
	.SingleGame img {
		border-radius: 3em;
		width: 100%;
		height: 100%;
	}

	.SingleGame-Text {

		position: absolute;
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%;
		height: 100%;
		background: #1E1E1DAA;
		visibility: hidden;
		opacity: 0;
		transition: all 1s ease;
		border-radius: 3em;
	}
	.SingleGame:hover .SingleGame-Text{
		visibility: visible;
		opacity: 1;
	}

	.SingleGame-Text a {
		background: #1E1E1D;
		width: 3em;
		height: 3em;
		border-radius: 50%;
		position: absolute;
		display: flex;
		justify-content: center;
		align-items: center;
		top: 40%;
		font-size: 1.3vw;
		font-weight: 600;
		color : #2FD0B8;
		border: 1px solid #2FD0B8AA;
		animation: Glow 2s ease-in-out infinite;
	}

	@keyframes Glow {
		0%{
			box-shadow: none;
			color: #FFF;
		}
		50%{
			box-shadow: 0px 0px 4px 2px  #2FD0B8;
			color: #2FD0B8;
		}
		100%{
			box-shadow: none;
			color: #FFF;
		}
	}