@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

.SuperCalculator {
	margin: 0;
	padding: 0;
	font-family: "Montserrat", sans-serif;
	position: relative;
	width: 25vw;
	height: 35vw;
	display: flex;
	align-items: center;
	justify-content: center;
	transform: translateY(120px);
	box-shadow: 0px 0px 6px 2px #63a0cb ;
	border-radius: 10px;
}